import React from 'react'
import './UnVettedList.css'
import pricead from '../Images/pricebotad.gif'
function UnVettedList() {
    return (
        <div className='unvettedlist'>
            <h3>Un-Vetted list</h3>            
            <p>The Un-Vetted list will place all new submissions to the top of the list, pushing the rest down. You can promote the same token as many times as you want, putting it back to the top of the list.</p>
            <p>If your token is pushed off the top-10 most recent deposits, it will no longer be on the list and you will have to deposit again to get it back onto the top.</p>
            <p>Place your token on the Un-Vetted list by depositing NaN WBNB <span className='green'>($NaN)</span> (You must wrap your regular BNB into WBNB on pancakeswap). This is non-refundable and is used to create POOCOIN/BNB LP (minus the 10% dev fee).</p>

         <div className= 'add_input'>
             <div className='input_block'>

            <label for='input'>Token Address <br></br><span>Must be a valid Binance Smart Chain token address</span></label> <br></br>
            <div className='tokenaddress'>
             <input type='textbox'  id= 'input' />                                    
            </div>  
            <label for='input'>Telegram URL <br></br><span>Must be a valid telegram group url starting with https://t.me/</span></label> <br></br>
            <div className='tokenaddress'>
             <input type='textbox'  id= 'input' />  
             <p>Your WBNB Balance is:-</p> 
             <div className='warning'>
             <h5>
                 Connect wallet to enable this form.
                 </h5>
             <p>After depositing, you will need to refresh the browser to see your token on the high-risk list.</p>
             </div>

             <img src ={pricead} width= '350' height='200'/>
             </div>                                 
            </div>  
        </div>
                </div>
    )
}

export default UnVettedList
