import React from 'react'
import './telegram_price_bot.css'
import pricebot from '../Images/pricebotad.gif'
import pricebot1 from '../Images/pricebot1.png'
import pricebot2 from '../Images/pricebot2.png'
import pricemenu from '../Images/price-bots-menu.jpg'
import pricedetails from '../Images/pricebot-details.jpg'
function Telegram_Price() {
    return (
        <div className='tpb-page'>
          <h3>Telegram Price Bot</h3>
          <p>This is the telegram pricebot used in the poocoin telegram group. Get one for your own channel by contacting <a>@dasilva333</a> on Telegram.</p>
            <img src={pricebot} width='400' height='242'/>
            <div className='flex-img'>
            <img src={pricebot1} width='400' height='242'/>
            <img src={pricebot2} width='400' height='242'/>
            </div>
            <div className='menu'>
            <img src={pricemenu} />
            <img src={pricedetails}/>
            </div>

        </div>
    )
}

export default Telegram_Price
