import React from 'react'
import {Link} from 'react-router-dom'
import './tools.css'
function Tools() {
    return (
        <>
        <div className='toolspage'>
            <h1>Tools</h1>
            <div className='toolslinks'>
            <Link to ='/tools/ape' >Ape (New token scanner)</Link> <br></br>
            <Link to = 'tools/toilet' >Toilet (Farm yields)</Link > <br></br>
           <Link to='tools/rugchek' >Rug Check</Link> <br></br>
            </div>       
        </div>
         
        </>
    )
}

export default Tools
