import React from 'react'
import './toolApe.css'
function Tool_Ape() {
    return (
        <div className='ape-page'>          
            <h1>Ape</h1>
            <h3>Scan for new BSC token launches.</h3>
            <p>This list auto-refreshes every block, you dont need to refresh the page.<br></br>Suggestions on what to check:</p>
            <ul className='list'>
                <li>Search the token name in Telegram, see if there is a Telegram group for the token.</li>
                <li>Click "Contract" and check there is no mint function.</li>
                <li>Click "Holders" and check most of the supply is in the LP.</li>
                <li>Click "BNB LP Holders" and check most of the supply is locked or burned.</li>
            </ul>
            <div className ='versions'>
            <label for="versions">LP Version</label>
            <select name="versions" id="versions">
            <option value="Version2">Version 2</option>
            <option value="Version1">Version 1</option> 
            </select>
            </div> 
            <div className= 'table'>
            
            <table>
                <tr className='heading'>
                    <th>Token</th>
                    <th>Creation Time</th>      
                    <th>Info</th>      
                    <th></th>      
                </tr>
                <tr className='row'>
                 <td>
                    <p className = 'table-row'>
                     <a href='#'>SAFEMOONKING<br></br> <p className='sub-heading'>SafeMoonKing</p></a>     
                    </p> 
                 </td>
                 <td className= 'price-star'>  
                 <td><a>04:26<p>0.3 Minutes since Creation</p></a></td>  
                 <td><a>Contract</a> <a>Holders</a><br></br><a>BNB LP Holders</a></td>                 
                 </td>   
                 <td>
                     <button>Ape</button>
                     </td>            
                </tr>             
            </table>            
        </div>               
        </div>
    )
}

export default Tool_Ape
