import React from 'react'
import {NavLink , Link} from 'react-router-dom'
import './header.css'
import logo from '../Images/poocoinlogo.png'
import telLogo from '../Images/telegram_logo.svg'
function Header() {
    return (
        <>
        <div className = 'header'>
            {/* In this portion logo, price and telegram icon is adde */}
        <div className = 'logo-portion'>
        <NavLink to ='/' href= '/'>
        <img src = {logo} className = 'poocoinlogo' alt = 'logo'  height = '35' width = '35'/>  
        </NavLink>
        <NavLink to='/' href ='#'>
        <div className = 'poocoin-text'>
        <span>PooCoin</span> <br></br>   
        <div>Charts</div> 
        </div>
        </NavLink>
        <div className= 'poocoin_price'>
        <a href= '#'>
         <div className='logowithwhiteBackground'>
        <img src = {logo} className = 'poocoinlogo' alt = 'logo'  height = '13' width = '18'/> 
            </div>
        <span className='price'>$4.12</span>
        </a>        
        </div>    
        <a target="_blank" rel="noreferrer" href="https://t.me/poocointokenchat" > <img src={telLogo} alt = 'telegram' height="25"/> </a>
        </div>           
        {/* In this portion Rest of the navigation bar is added */}         
        <nav className="navbar">
            <ul className="navbar-ul">
                <li><NavLink to='/' className="item" activeClassName='item-active'>Charts</NavLink></li>
                <li><NavLink to='/buycoin' className="item" activeClassName='item-active'  href='#'>BuyPooCoin</NavLink></li>
                <li><NavLink to='/tools' className="item" activeClassName='item-active' href='#' >Tools</NavLink> </li>
                <li><NavLink to='/premium' className="item" activeClassName='item-active' href='#' >Premium</NavLink> </li>
                <li><NavLink to='/promote' className="item" activeClassName='item-active'  href='#' >Promote</NavLink> </li>
        {/* This portion is of connect to vallet button */}
        <div className= 'connect'>
            <button>Connect</button>
        </div>
            </ul>    
        </nav>  
        </div>              
    </>
    )
}
export default Header
