import React from 'react'
import './premium.css'
import walletImage from '../Images/wallettrack.png'
import walletImage2 from '../Images/wallettrack2.png'
import walletImage3 from '../Images/wallettrack3.png'
import buysell from '../Images/buysell.png'
function Premium() {
  
    return (
        <div className='premium-page'>
           <h1>Premium</h1>
           <p>Unlock premium level 1 by holding <span>$100</span> worth of <a href='https://exchange.pancakeswap.finance/#/add/BNB/0xb27adaffb9fea1801459a1a81b17218288c097cc'>POOCOIN/BNB LP</a> <br></br>
           (approximately <strong>0.000008 POOCOIN/BNB LP</strong> created from <strong>14.6018 <a href='https://exchange.pancakeswap.finance/#/add/BNB/0xb27adaffb9fea1801459a1a81b17218288c097cc'>POOCOIN</a></strong> and <strong> 0.1020 BNB</strong>) <br>
           </br> tokens in your wallet</p>
           <p>Unlock premium level 1 by holding <span>$300</span> worth of <a href='https://exchange.pancakeswap.finance/#/add/BNB/0xb27adaffb9fea1801459a1a81b17218288c097cc'>POOCOIN/BNB LP</a> <br></br>
           (approximately <strong>0.000025 POOCOIN/BNB LP</strong> created from <strong>44.056 <a href='https://exchange.pancakeswap.finance/#/add/BNB/0xb27adaffb9fea1801459a1a81b17218288c097cc'>POOCOIN</a></strong> and <strong> 0.307 BNB</strong>) <br>
           </br> tokens in your wallet</p>

           <h2>
               Premium features
           </h2>
           <p>The current features are:</p>
           
           <div className='features'>
               <h3>Tier 1: Track other wallets </h3>
               <p>Click "Track" on a transaction to track the trader's wallet</p>
               <img src={walletImage} />
               <p>This will change the wallet token list to show their wallet instead of your own.</p>
               <img src={walletImage2} />
               <p>Click the "Clear" button to go back to your own wallet.</p>
               <p>You can also track a wallet by entering the wallet address into the input field and clicking "Go"</p>
               <img src={walletImage3} />

           </div>
           <div className='features'>
               <h3>Tier 1: Disable ads </h3>
               <p>Disable ads by checking the box</p>
               <input type="checkbox" id="ads" name="ads" value="ads"/>
               <label for="ads">Disable ads (premium feature)</label>       
        </div>
           <div className='features'>
               <h3>Tier 2: Show your trades </h3>
               <p>Show your trades plotted on the chart, and enable the "Wallet tx" tab.</p>
               <img src={buysell} />
                   
        </div>
        </div>
    )
}

export default Premium
