import React from 'react'
import './promote.css'
import {BrowserRouter, Link, Switch, Route}  from 'react-router-dom'
import UnVettedList from './UnVettedList'
function Promote() {
    return (
        <div className='promote-page'>
            <h1>Promote your token</h1> 
            <div className='alert'>
                <p>The history of all information entered here will be publicly visible on the chain.</p>

            </div>
             <div className='disablecheck'>
               <input type="checkbox" id="ads" name="ads" value="ads"/>
               <label for="ads">Disable ads (premium feature)</label>                      
             </div>           
             <div className='promote-nav'>                
             <ul className="navbar-ul sub-navbar ">
                <li><Link to='/promote/unvettedlist' className="item1"  href='#'>Un-Vetted List</Link></li>
                <li><Link to = '/promote/telegram-price-bot'className="item1" href='#' >Vetted List</Link> </li>
                <li><Link to='/promote/telegram-price-bot' className="item1" href='#'>Banner</Link></li>
                <li><Link to='/promote/telegram-price-bot' className="item1" href='#' >Telegram Price Bot</Link> </li>
            </ul> 
                  
                   
             </div>           
        </div>
    )
}

export default Promote
