import React from 'react'
import './buycoin.css'
import logo from '../Images/poocoinlogo.png'
import Chart from '../charts of coins/Chart'
function Buycoin() {
    return (
        <div className ='buycoin-page'>
            <div className='coinbuy'>
           <h1>POO COIN </h1><button>Buy</button>
            </div>
            <div className = 'pricewithlogo'>  
            <div className='whitebg'>
            <img src = {logo} className = 'poocoinlogo' alt = 'logo'  height = '22' width = '22'/> 
                </div>         
            <div className='price'>$3.949435</div>
            </div>
            <h2>
            Reflect token on the Binance Smart Chain.
            </h2>
            <h3>A 8% fee is charged on each transaction. 4% is distributed to other token holders and 4% is burned.
            </h3>
            <h3>Initial supply: 10,000,000 no more than 100,000 can be traded in 1 transaction. </h3>

            <div className='links'>
            <a>Twitter</a> <br></br>
            <a>Telegram</a> <br></br>
            <a>Reddit</a> <br></br>
            <a>Discord</a> <br></br>
            </div>

            <Chart />

            
        </div>
    )
}

export default Buycoin
