import './App.css';
import {Switch, Route} from 'react-router-dom'
import Header from './Header/Header'
import Home from './Homepage/Home'
import Tools from './tools/Tools'
import Premium from './Premium/Premium'
import Buycoin from './buycoin/Buycoin';
import Promote from './promote/Promote'
import Tool_Ape from './tools/Tool_Ape'
import Tools_Toilet from './tools/Tools_Toilet';
import Tool_RugCheck from './tools/Tool_RugCheck';
import Chart from './charts of coins/Chart';
import UnVettedList from './promote/UnVettedList';
import Telegram_Price from './promote/Telegram_Price_Bot';
import BannerAd from './promote/BannerAd';
function App() {
  return (
    <div className="App">
        <Header />   
        <Switch>    
            <Route exact path='/' component ={Home} ></Route>
            <Route exact path='/buycoin' component ={Buycoin} ></Route>
            <Route exact path='/tools' component ={Tools} ></Route>
            <Route exact path ='/tools/ape' component ={Tool_Ape}></Route>
            <Route exact path ='/tools/toilet' component ={Tools_Toilet}></Route>
            <Route exact path ='/tools/rugchek' component ={Tool_RugCheck}></Route>
            <Route exact path='/premium' component ={Premium} ></Route>
            <Route exact path='/token' component ={Chart} ></Route>         
            <Route exact  path='/promote' component ={Promote} ></Route>         
            <Route exact path='/promote/unvettedlist' component ={UnVettedList} ></Route>         
            <Route exact path='/promote/telegram-price-bot' component ={Telegram_Price} ></Route>         
            <Route exact path='/promote/banner' component ={BannerAd} ></Route>         
            <Route exact path='/promote/banner' component ={BannerAd} ></Route>         
        </Switch>        
    
        </div>
  );
}
export default App;
